export default {
	data() {
		return {
			user: [],
			opp: [],
			oScore: [],
			uScore: [],
		}
	},
	onLoad: function() {
		uni.request({
			url: 'http://' + getApp().globalData.urlStr + '/GetAllInfoById',
			method: 'GET',
			data: {
				id: getApp().globalData.uId,
			},
			success: res => {
				this.user = res.data.data.user;
				this.opp = res.data.data.opp;
				this.uScore = res.data.data.uScore;
				this.oScore = res.data.data.oScore;
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
